.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}
.gallery-columns-2 .gallery-item {
  max-width: 50%;
}
.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}
.gallery-columns-4 .gallery-item {
  max-width: 25%;
}
.gallery-columns-5 .gallery-item {
  max-width: 20%;
}
.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}
.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}
.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}
.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-caption {
  display: block;
}

/* Fixed header fix */
.admin-bar .fixed-top,
.admin-bar .sticky-top {
  top: 46px;
}
@media (min-width: 783px) {
  .admin-bar .fixed-top,
  .admin-bar .sticky-top {
    top: 32px;
  }
}

.mw-300 {
  max-width: 300px;
}

.mw-400 {
  max-width: 400px;
}

.mw-500 {
  max-width: 500px;
}

.mw-600 {
  max-width: 600px;
}

.mw-700 {
  max-width: 700px;
}

.mw-800 {
  max-width: 800px;
}

.mt-neg-200 {
  margin-top: -200px;
  position: relative;
  z-index: 1;
}

.z-0 {
  z-index: 0;
}

.z-1 {
  z-index: 1;
}

.z-2 {
  z-index: 2;
}

.pagination {
  margin-top: 2rem;
  justify-content: center;
}

.comments-area {
  margin-top: 3rem;
}

.fixed-cta,
.back-to-top {
  position: fixed;
  bottom: -300px;
  opacity: 0;
  transition: all 0.6s ease-in-out;
  z-index: 900;
}

.fixed-cta {
  right: 1rem;
}
@media (min-width: 783px) {
  .fixed-cta {
    right: 2rem;
  }
}

.back-to-top {
  left: 1rem;
  bottom: -100px;
}
@media (min-width: 783px) {
  .back-to-top {
    left: 2rem;
  }
}

.scrolled-up .fixed-cta,
.scrolled-up .back-to-top {
  opacity: 0.96;
  bottom: 1rem;
}
@media (min-width: 783px) {
  .scrolled-up .fixed-cta,
  .scrolled-up .back-to-top {
    bottom: 2rem;
  }
}

.back-to-top {
  width: 3rem;
  height: 3rem;
  background-color: #005431;
  border-radius: 0.375rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  border: 1px solid transparent;
  color: #fff;
  transform: rotate(270deg);
  background-image: url("../img/flecha-derecha-blanca.svg");
  background-size: 1.5rem auto;
  background-position: center;
  background-repeat: no-repeat;
}
.back-to-top:hover {
  background-color: #002113;
  border: 1px solid #002113;
}

#search-wrapper,
#error-404-wrapper {
  padding-top: 10rem;
}
#search-wrapper .page-header,
#error-404-wrapper .page-header {
  margin-bottom: 2rem;
}

#no-title-page-wrapper,
#woocommerce-wrapper {
  padding-top: 0;
}

#narrow-width-page-wrapper .entry-title {
  text-align: center;
}
#narrow-width-page-wrapper .entry-content {
  max-width: 800px;
  margin: 0 auto;
}
#narrow-width-page-wrapper .wp-block-heading {
  margin-top: 2em;
}

body.home #no-title-page-wrapper {
  padding-bottom: 0;
}

#wrapper-top-bar {
  padding: 0.3rem 0;
  font-size: 0.875rem;
  background-color: #E1DAD6;
  border-bottom: 1px solid #908884;
}
#wrapper-top-bar p {
  margin-bottom: 0;
}
#wrapper-top-bar .global-links .wp-block-buttons .wp-block-button .wp-block-button__link {
  padding: 0;
  background-color: transparent !important;
  border: 0;
  margin: 0 0.25rem;
  text-transform: none;
}
#wrapper-top-bar .global-links .wp-block-buttons .wp-block-button .wp-block-button__link .btn-arrow {
  display: none;
}

:root :where(p.has-background) {
  padding: 2rem;
}

body.navbar-transparent #wrapper-navbar {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
}
body.navbar-transparent #wrapper-navbar + .wrapper {
  padding-top: 0;
}

body.scrolled #wrapper-navbar {
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
}

.stretch-linked-block {
  position: relative !important;
  transition: opacity 0.15s linear;
}
.stretch-linked-block .editorskit-block-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 90;
}
.stretch-linked-block:hover {
  opacity: 0.7;
}
.stretch-linked-block:hover .wp-block-button.is-style-arrow-link .wp-block-button__link:after {
  right: 0;
}

.stretched-link:hover, .wp-block-group.is-style-featured-shadow a:hover, .wp-block-cover.is-style-reveal a:hover {
  text-decoration: none;
}

.entry-footer {
  position: relative;
  z-index: 2;
}

.card-body > *:last-child {
  margin-bottom: 0;
}

#accordion-infografia {
  --bs-accordion-bg: transparent;
}

