.slick-prev,
.slick-next {
  z-index: 1;
  width: calc( 24px + 12px );
  height: calc( 24px + 12px );
  background-color: #E9E4E1;
  border-radius: 50rem;
  border: 1px solid #908884;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.slick-prev:hover, .slick-prev:focus, .slick-prev:active,
.slick-next:hover,
.slick-next:focus,
.slick-next:active {
  background-color: #fff;
}
.slick-prev:before,
.slick-next:before {
  font-size: 0;
  line-height: 0;
  width: 14px;
  height: 24px;
  margin-left: 4px;
}
.slick-prev:before,
.slick-next:before {
  font-family: unset;
}
.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  opacity: 0.25;
}

.slick-prev {
  left: -24px;
}
.slick-prev:before {
  content: url("data:image/svg+xml,%3Csvg width='14' height='24' viewBox='0 0 14 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 22L2 12L12 2' stroke='%23000000' stroke-width='2' stroke-linecap='square'/%3E%3C/svg%3E");
  margin-right: 6px;
}

.slick-next {
  right: -24px;
}
.slick-next:before {
  content: url("data:image/svg+xml,%3Csvg width='14' height='24' viewBox='0 0 14 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 22L12 12L2 2' stroke='%23000000' stroke-width='2' stroke-linecap='square'/%3E%3C/svg%3E");
  margin-left: 4px;
}

.alignfull .slick-prev {
  left: 1rem;
}
.alignfull .slick-next {
  right: 1rem;
}

.slick-slider-default .slick-prev:before,
.slick-slider-default .slick-next:before {
  color: #fff;
  transition: all 0.6s ease-in-out;
  filter: drop-shadow(0 0 2px #000);
}

.slick-carousel,
.is-style-slick-carousel,
.slick-carousel-padded {
  position: relative;
  /*
  .slick-prev,
  .slick-next {
  	top: unset;
  	// bottom: -$slick-font-size*1.5;
  	bottom: -$slick-icon-height*1.5;
  	transform: none;
  }

  .slick-prev {
  	left: unset;
  	// right: $slick-font-size*1.2;
  	right: $slick-icon-width*2;
  }

  .slick-next {
  	right: 0;
  }	
  */
}
.slick-carousel .slick-list,
.is-style-slick-carousel .slick-list,
.slick-carousel-padded .slick-list {
  margin: -0.5rem;
}
.slick-carousel .slick-slide,
.is-style-slick-carousel .slick-slide,
.slick-carousel-padded .slick-slide {
  margin: 0 0.5rem;
  transition: opacity 0.15s linear;
}
.slick-carousel .slick-slide:not(.slick-active),
.is-style-slick-carousel .slick-slide:not(.slick-active),
.slick-carousel-padded .slick-slide:not(.slick-active) {
  opacity: 0.5;
}
.slick-carousel .slick-slide.is-style-featured-shadow,
.is-style-slick-carousel .slick-slide.is-style-featured-shadow,
.slick-carousel-padded .slick-slide.is-style-featured-shadow {
  margin: 2rem 0.5rem;
}

.slick-padded .slick-list {
  padding-right: 100px;
}
@media (min-width: 783px) {
  .slick-padded .slick-list {
    padding-right: 70px;
  }
}
.slick-padded .slick-slide {
  margin: 0;
}

.slick-slider .wp-block-cover {
  min-height: 450px;
}
.slick-slider .wp-block-cover .wp-block-cover__inner-container {
  position: relative;
}
.slick-slider.wp-block-gallery .wp-block-image img {
  aspect-ratio: 1;
}

.slick-carousel-same-height .slick-track {
  display: flex !important;
}
.slick-carousel-same-height .slick-slide {
  height: inherit !important;
}

.slick-slider.slick-dotted {
  margin-bottom: calc( 1rem + 24px );
}

.slick-dots {
  bottom: -1rem;
  display: flex;
  justify-content: center;
  gap: 2px;
}
.slick-dots > li {
  display: inline-block;
}
.slick-dots > li:only-child {
  display: none;
}
.slick-dots li {
  flex: 1 1 auto;
  max-width: 2rem;
  height: 2px;
  margin: 0;
  background-color: #005431;
  opacity: 0.3;
  transition: all 0.6s ease-in-out;
}
.slick-dots li button {
  padding: 6px 0;
  width: 2rem;
  height: 2px;
}
.slick-dots li button:before {
  content: none;
}
.slick-dots li.slick-active, .slick-dots li:hover {
  opacity: 1;
}

.slick-arrows-bottom {
  margin-bottom: calc( 3rem + 24px ) !important;
}
.slick-arrows-bottom .slick-next,
.slick-arrows-bottom .slick-prev {
  top: unset;
  left: unset;
  bottom: calc( -24px - 2rem );
  transform: none;
}
.slick-arrows-bottom .slick-prev {
  right: calc( 14px + 2rem );
}
.slick-arrows-bottom .slick-next {
  right: 0;
}
.slick-arrows-bottom .slick-dots {
  bottom: -2.5rem;
  padding-right: calc( 28px + 6rem );
  text-align: left;
  justify-content: flex-start;
}
@media (min-width: 783px) {
  .slick-arrows-bottom .slick-dots {
    padding-left: calc( 28px + 4rem );
    text-align: center;
    justify-content: center;
  }
}
.slick-arrows-bottom.alignfull .slick-next,
.slick-arrows-bottom.alignfull .slick-prev {
  margin-right: 1rem;
}

@media (min-width: 783px) {
  .slick-arrows-left .slick-prev {
    right: unset;
    left: 0;
  }
  .slick-arrows-left .slick-next {
    right: unset;
    left: calc( 14px + 2rem );
  }
}

